@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@500;700;800&family=Raleway:ital,wght@0,100;0,200;0,400;0,600;0,900;1,600;1,900&display=swap');

*, body {
    left: 0px;
    top: 0px;
    margin: 0px;
    overflow-x: hidden; }

section {
    height: 100vh;
    position: relative;
    width: 100vw; }

.container {
    height: 100%;
    margin-left: 10%;
    margin-right: 10%;
    width: 100%; }

.sectionheader {
    position: absolute;
    display: flex;
    margin-left: 2%;
    height: 100%;
    width: 100%;
    text-transform: uppercase;
    font-family: 'Raleway', sans-serif;
    font-weight: 600;

    & span {
        justify-content: center;
        align-items: start;
        height: 100%;
        width: 100%;
        margin-top: 5%;
        & h1 {
            font-size: 13rem; }
        & h2 {
            font-size: 10rem; } } }

.sozialnav {
    height: 100%;
    width: 30%;
    position: absolute;
    background-color: blue;
    color: white;



    .sozialicon {
        height: 20%;
        width: auto; }
    justify-content: center;
    align-items: start; }

.video-container {
    display: flex;
    height: 100%;
    overflow: hidden;
    justify-content: flex-end; }

.video {
    height: 80%;
    margin: auto;
    margin-top: 20%; }





